.aboutMe__container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 10px;
  padding-bottom: 50px; }
  .aboutMe__container .photo {
    width: 250px;
    height: 315px;
    opacity: 0.9;
    border-radius: 50px;
    cursor: auto; }
    .aboutMe__container .photo:hover {
      opacity: 1; }
  .aboutMe__container .summary {
    width: 20vw;
    margin-top: 15px; }
  .aboutMe__container .photoSummary {
    width: 30%; }
  .aboutMe__container .info {
    margin-top: 60px;
    width: 60%; }
    .aboutMe__container .info .info__heading {
      font-weight: bold; }
    .aboutMe__container .info .info__paragraph {
      width: 50vw; }
    .aboutMe__container .info .btn {
      margin-top: 0px;
      margin-bottom: 20px; }

@media screen and (max-width: 600px) {
  .photo {
    width: 230px;
    margin: 0 auto;
    border-radius: 0; }
  .aboutMe__container {
    flex-direction: column;
    padding-bottom: 80px;
    justify-content: center;
    width: 80vw; }
  .summary {
    width: 100vw; }
  .info {
    margin-top: 0; } }

@media screen and (max-width: 900px) {
  .aboutMe__container {
    margin: 0 auto; } }
