.logo {
  width: 100px;
  height: 110px;
  margin-top: 2px;
  margin-left: 10px; }
  .logo:hover {
    width: 105px;
    cursor: pointer; }

.header {
  display: flex;
  flex-direction: row;
  justify-content: center; }

.navButtons {
  position: fixed;
  right: 7vw;
  top: 2vh;
  display: flex;
  flex-direction: column; }
  .navButtons .navButton {
    width: 50px;
    height: 50px;
    margin-bottom: 15px;
    padding: 1px; }
    .navButtons .navButton:hover {
      cursor: pointer;
      border: 2px solid #84FF41;
      border-radius: 10px; }

@media screen and (max-width: 500px) {
  .navButtons {
    right: 0;
    position: absolute; } }
