.toggleButton {
  text-align: center; }
  .toggleButton .btn {
    margin-top: 0;
    margin-left: 10px;
    margin-right: 10px; }

.nutrition__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 20px;
  padding-bottom: 100px; }
  .nutrition__container .video, .nutrition__container .text {
    width: 35vw; }
  .nutrition__container .video {
    padding-left: 20px; }
    .nutrition__container .video .video__name {
      font-size: 23px;
      padding-left: 15px;
      border-top: 1px solid black;
      border-bottom: 1px solid black;
      font-family: Pacifico, cursive;
      width: inherit; }
  .nutrition__container .text {
    margin-right: 70px;
    -webkit-box-shadow: 4px 4px 31px 1px rgba(65, 255, 84, 0.74);
    -ms-box-shadow: 4px 4px 31px 1px rgba(65, 255, 84, 0.74);
    box-shadow: 4px 4px 31px 1px rgba(65, 255, 84, 0.74);
    padding: 15px; }
    .nutrition__container .text .important {
      font-weight: 800;
      text-align: center; }
    .nutrition__container .text .btn {
      margin-top: 0; }
  .nutrition__container .photo {
    width: 10vw;
    position: fixed;
    top: 50%;
    left: 44.5%; }
    .nutrition__container .photo img {
      width: 100px;
      height: 100px; }
