.toggleButton {
  margin-top: 10px; }

.motivation__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-bottom: 100px;
  margin-right: 20px;
  width: 82vw; }
  .motivation__container .video {
    margin-top: 10px;
    margin-left: 20px; }
    .motivation__container .video .video__name {
      font-size: 23px;
      padding-left: 15px;
      border-top: 1px solid black;
      border-bottom: 1px solid black;
      font-family: Pacifico, cursive; }
  .motivation__container .text {
    -webkit-box-shadow: 4px 4px 31px 1px rgba(65, 255, 84, 0.74);
    -ms-box-shadow: 4px 4px 31px 1px rgba(65, 255, 84, 0.74);
    box-shadow: 4px 4px 31px 1px rgba(65, 255, 84, 0.74);
    font-family: cursive;
    margin-top: 20px;
    margin-right: 20px;
    width: 40vw;
    padding-left: 10px;
    padding-right: 5px; }
    .motivation__container .text .important {
      font-weight: 800;
      padding-top: 15px;
      padding-bottom: 10px; }
    .motivation__container .text .btn {
      margin-top: 10px;
      margin-left: 10px; }
    .motivation__container .text .new-topic {
      padding-top: 15px; }

@media screen and (max-width: 1100px) {
  .motivation__container {
    flex-direction: column;
    width: 50vw;
    justify-content: center;
    margin: 0 auto;
    padding-bottom: 120px; }
    .motivation__container .video__name {
      text-align: center; }
    .motivation__container .text {
      width: 55vw;
      text-align: center; } }
