* {
  box-sizing: border-box;
  scroll-behavior: smooth; }

*::selection {
  background: #99ffa3; }

body {
  font-family: cursive;
  background-color: #41ff54; }
  body .App {
    background-color: #FFFFFF;
    width: 86vw;
    margin: 0 auto; }
  body button {
    cursor: pointer;
    margin-top: 20vh; }

.footer {
  position: fixed;
  padding-bottom: 10px;
  padding-top: 10px; }

@media screen and (max-width: 500px) {
  .App {
    width: 100vw; }
  body {
    background-color: white;
    font-family: sans-serif; } }
